@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=IBM+Plex+Mono:wght@500&family=Pacifico&family=Permanent+Marker&family=Qahiri&family=Ubuntu&family=Zen+Kurenaido&display=swap);
*{
  margin:0;
  padding: 0;
   color: white;
  /* background:linear-gradient(-45deg,rgb(149, 117, 212),rgb(115, 120, 148),rgb(235, 230, 236));  */
}


.navbar{
    border: 10px double linear-gradient(-45deg,rgb(230, 36, 22),rgb(0, 36, 240),rgb(9, 241, 40));
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 35vh;
    flex-flow: column;
    color: #fff;
    background:linear-gradient(-45deg,rgb(175, 135, 255),rgb(54, 78, 218),rgb(191, 40, 241));
    font-family: 'IBM Plex Mono', monospace;
    position: relative;
    background-size: 400% 400%;
    -webkit-animation: change ease-in-out 10s infinite;
            animation: change ease-in-out 10s infinite;
    border-bottom:5px solid #00010f;
    z-index: 25;
}

.maincard{
    margin-top: 20px;
    padding: 10px;
    width: 300px;
    height: 450px;
    display: flex;
    flex-direction: column;
    background:linear-gradient(-45deg,rgb(104, 99, 116),rgb(26, 31, 61),rgb(6, 0, 8));
    border-radius: 20px;
}

.maincard:hover{
    box-shadow: #00010f 0rem 1rem 1rem;
    -webkit-transform: translateX(-1rem) ;
            transform: translateX(-1rem) ;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.cardarea{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.row{
    display: flex;
    justify-content: space-evenly;
}
.footer {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Permanent Marker', cursive;
    background:linear-gradient(45deg,rgb(233, 227, 227),rgb(0, 0, 19)) 10;
  }
@-webkit-keyframes change{

    0%{
        background-position: 0 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0 50%;
    }
}
@keyframes change{

    0%{
        background-position: 0 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0 50%;
    }
}

blockquote{
    font-size: 1em;
    width:70%;
    margin: 0px;
    font-family:Open Sans;
    font-style:italic;
    color: white;
    padding:0.5em 10px 1.2em 10px;
    border-left:6px solid #ffffff ;
    line-height:0.8;
    position: relative;
    background:transparent;
  }
  
  blockquote::before{
    font-family:Arial;
    content: "\201C";
    color:#feffff;
    font-size:3em;
    position: absolute;
    left: 5px;
    top:-10px;
  }
  
  blockquote::after{
    content: '';
  }
  
  blockquote span{
    display:block;
    color:white;
    font-style: normal;
    font-weight: bold;
    margin-top:0.5rem;
  }

  .footerarea{
    width: 100%;
    height: 4rem;
    background: linear-gradient(-45deg,rgb(104, 99, 116),rgb(26, 31, 61),rgb(6, 0, 8));
    color: white;
    text-align: center;
    border-top:5px solid #00010f;
    font-family: 'Zen Kurenaido', sans-serif;
  }

  @media screen and (max-width: 600px) {
    .row {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  @media screen and (max-width: 1024px) {
    .row {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
